.nav__main {
  width: 980px;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 40px;
  display: flex;
}
.nav__navigation-items {
  display: flex;
  padding: 0;
  justify-content: center;
}
.nav__navigation-items li {
  margin-right: 25px;
  list-style: none;
  display: block;
  padding: 10px;
}
.nav__navigation-items a {
  color: #ffffff;
}
.nav__navigation-start {
  border-bottom: 1px solid white;
}
.case__item-1 {
  width: 980px;
  margin: 0 auto;
  position: relative;
}
.case__item-1::before {
  position: absolute;
  content: "";
  top: 227px;
  left: 123px;
  width: 404px;
  height: 439px;
  background-image: url("/img/icon2.png");
}
.case__item-main-items1 {
  display: flex;
  justify-content: space-between;
  margin-bottom: 60px;
}
.case__item-main {
  position: relative;
  height: 540px;
  width: 309px;
  border-radius: 35px;
  background-color: #9827e8;
  transition: all 0.3s ease-out;
}
.case__item-main::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 307px;
  height: 540px;
  background-image: url("/img/figure-1.png");
}
.case__item-main:hover {
  transform: scale(1.03);
}
.case__item-wrapper {
  position: absolute;
  top: 33px;
  width: 100%;
  z-index: 100;
}
.case__item-title {
  margin: 0;
  text-align: center;
  font-size: 25px;
  margin-bottom: 169px;
  color: #fff;
}
.case__item-intro {
  margin: 0;
  color: #ffffff;
}
.case__item-image {
  margin-top: 47px;
  z-index: 10;
  position: relative;
}
.case__item-buy {
  position: relative;
  display: block;
  text-transform: uppercase;
  font-size: 16px;
  width: 120px;
  z-index: 100;
  padding: 10px;
  border-radius: 25px;
  margin-left: auto;
  margin-right: auto;
  margin-top: -90px;
  text-align: center;
  color: white;
  border: 1px solid white;
}
.random__main {
  background-image: url("/img/bg2.png");
  padding-bottom: 20px;
  padding-top: 20px;
}
.random__main-container {
  width: 980px;
  margin: 0 auto;
  display: flex;
  justify-content: flex-end;
  position: relative;
  align-items: center;
  padding-top: 49px;
  padding-bottom: 50px;
}
.random__main-container::after {
  position: absolute;
  content: "";
  top: -100px;
  left: -90px;
  width: 464px;
  height: 499px;
  background-repeat: no-repeat;
  background-image: url("/img/giddy-up-fortnite-skin.png");
}
.random__item {
  width: 220px;
  height: 262px;
  margin-left: -7px;
  border-radius: 25px;
  transition: all 0.3s ease-out;
  background-color: #d6d13d;
}
.random__item:hover {
  transform: scale(1.1);
}
.random__item-size1 {
  background-color: #b9b8b8;
}
.random__item-size2 {
  width: 260px;
  height: 280px;
}
.random__item-size3 {
  background-color: #525151;
}
.random__title {
  margin: 0;
  font-family: Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 30px;
  color: #fff;
  margin-bottom: 38px;
  margin-top: 36px;
  text-align: center;
}
.random__intro {
  margin: 0;
  margin-bottom: 18px;
  font-size: 14px;
  color: white;
  text-align: center;
}
.random__price {
  margin: 0;
  color: white;
  font-size: 25px;
  text-align: center;
  margin-bottom: 13px;
}
.random__buy {
  display: block;
  padding: 10px;
  width: 130px;
  margin: 0 auto;
  text-align: center;
  border: 1px solid #ffffff;
  border-radius: 20px;
  color: #ffffff;
}
.random__buy:hover {
  background: #ffffff;
  color: #000000;
}
.case__random {
  display: flex;
  position: relative;
  height: 239px;
  width: 450px;
  background: #9827e8;
  margin: 0 auto;
  border-radius: 15px;
  margin-bottom: 50px;
  transition: all 0.6s ease-out;
}
.case__random:hover {
  transform: scale(1.06);
}
.case__random-main {
  position: relative;
  display: flex;
}
.case__random-main::before {
  content: '';
  position: absolute;
  top: -50px;
  left: -100px;
  width: 1200px;
  height: 451px;
  background-image: url("/img/figure-bg.png");
}
.case__random::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 450px;
  height: 269px;
  background-repeat: no-repeat;
  background-image: url("/img/figure-3.png");
}
.case__random-random {
  position: relative;
}
.case__random img {
  z-index: 10;
  margin-left: -151px;
}
.case__random-wrapper {
  display: block;
  width: 320px;
  z-index: 100;
  margin-left: 32px;
}
.case__random-wrapper p {
  width: 165px;
  color: #ffffff;
}
.case__random-title {
  font-size: 25px;
  margin-top: 50px;
  margin-bottom: 27px;
}
.case__random-intro {
  font-size: 16px;
}
.game__main-container {
  display: flex;
  flex-wrap: wrap;
  position: relative;
}
.game {
  width: 980px;
  margin: 0 auto;
}
.game__item {
  width: 140px;
  margin-right: 20px;
  margin-bottom: 60px;
  position: relative;
  transition: all 0.6s ease-out;
}
.game__item:hover {
  transform: scale(1.04);
}
.game__item-wrapper {
  position: absolute;
  width: 100%;
  top: 60px;
  left: 0;
}
.game__item p {
  margin: 0;
  text-align: center;
  margin-bottom: 10px;
  color: #ffffff;
}
.game__item-title {
  text-transform: uppercase;
  font-size: 14px;
}
.game__item-title-big {
  font-size: 25px;
}
.game__item-buy {
  display: block;
  margin-top: -40px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  color: black;
  background: #9130e3;
  width: 140px;
  border-bottom-right-radius: 13px;
  border-bottom-left-radius: 13px;
}
.game__item-buy:hover {
  background-color: #801dca;
  color: #ffffff;
}
.game__item img {
  border-radius: 15px;
}
.game__item-big {
  width: 300px;
}
.game__item-big img {
  height: 365px;
}
.game__item-buy-big {
  width: 300px;
  margin-top: -58px;
}
.game__items-page {
  margin: 0 auto;
  margin-top: 30px;
  width: 980px;
  display: flex;
}
.game__item-big-page {
  width: 400px;
}
.game__item-buy-big-page {
  width: 400px;
}
.key__random-main {
  width: 980px;
  margin: 0 auto;
  display: flex;
}
.key__random-images {
  display: flex;
}
.key__random-images h2 {
  font-size: 35px;
  padding-left: 20px;
  width: 251px;
}
.key__random-images img {
  border-radius: 50px;
}
.key__random p {
  margin: 0;
  margin-top: 30px;
  font-size: 14px;
}
.key__random-info-wrapper {
  display: flex;
}
.key__random-info-wrapper p {
  margin-right: 10px;
}
.key__random-buy {
  display: inline-flex;
  justify-content: center;
  font-family: "FuturaPT", sans-serif;
  align-items: center;
  border-radius: 60px;
  font-size: 15px;
  padding: 30px;
  font-weight: 600;
  transition: 0.3s;
  cursor: pointer;
  border: none;
  color: #1a1a18;
  background: linear-gradient(to right, #fa8e00, #fbcc00);
  background-size: auto 200%;
  position: relative;
  text-transform: uppercase;
  top: 0;
}
.key__random-buy:hover {
  color: #ffffff;
  box-shadow: 0px 4px 15px rgba(250, 255, 0, 0.72);
  transform: translateZ(300px);
}
.key__random-price-main {
  display: flex;
}
.key__random-price {
  font-size: 90px;
  margin: 0;
  font-weight: 600;
}
.key__random-price-rub {
  font-weight: 600;
}
.footer {
  background-color: #3a3a3a;
}
